import { BuilderElement } from '@builder.io/react';

const loadOnHosts = ['builder.io', 'localhost:1234'];
const builderIdMap = new WeakMap<Element, string>();
const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

if (loadOnHosts.includes(location.host)) {
  document.addEventListener('DOMContentLoaded', function (event) {
    document.documentElement.classList.add('builder-extension-loaded');
  });
}

function traverseElement(element: any, cb: (el: any) => void) {
  if (element) {
    cb(element);
    if (element.children) {
      element.children.forEach((el: any) => traverseElement(el, cb));
    }
  }
}

function appendDifferencesForSize(
  size: 'medium' | 'small',
  map: { [key: string]: any },
  resultMap: { [key: string]: any },
  mediumMap: { [key: string]: any }
) {
  const isNumberString = (str: string) => String(parseFloat(str)) === str;
  for (const key in map) {
    if (map.hasOwnProperty(key) && !isNumberString(key)) {
      const value = map[key];
      const largeValue = resultMap[key];
      const mediumValue = mediumMap[key];
      if (value && largeValue) {
        const styles = value.responsiveStyles && value.responsiveStyles.large;
        const largeStyles = largeValue.responsiveStyles && largeValue.responsiveStyles.large;
        const mediumStyles =
          (mediumValue && mediumValue.responsiveStyles && mediumValue.responsiveStyles.large) || {};

        if (styles) {
          const differentObj: { [key: string]: string } = {};
          for (const key in styles) {
            if (styles.hasOwnProperty(key) && !isNumberString(key)) {
              if (styles[key] !== largeStyles[key]) {
                if (size === 'medium' || (size === 'small' && mediumStyles[key] !== styles[key])) {
                  differentObj[key] = styles[key];
                }
              }
            }
          }
          if (objectSize(differentObj)) {
            largeValue.responsiveStyles[size] = differentObj;
          }
        }
      }
    }
  }
}

const objectSize = (object: any) => Object.keys(object).length;

function elementsById(element: any) {
  const map: { [id: string]: any } = {};
  traverseElement(element, el => {
    if (el.id) {
      map[el.id] = {
        ...el,
        children: undefined,
      };
    }
  });
  return map;
}

let map: any = undefined;

let isCopyLayout = false;
const primaryColor = `rgb(26, 115, 235, 1)`;

function getKeyboardHintBox() {
  const existing = document.getElementById('builder-keyboard-hint');
  if (existing) {
    return existing;
  }
  const hint = document.createElement('div');
  hint.id = 'builder-keyboard-hint';
  Object.assign(hint.style, {
    position: 'fixed',
    bottom: '20px',
    right: '20px',
    padding: '12px 16px',
    backgroundColor: 'rgba(0, 0, 0, 0.8)',
    backdropFilter: 'blur(10px)',
    color: 'white',
    borderRadius: '8px',
    fontFamily: 'Inter, Helvetica, sans-serif',
    fontSize: '14px',
    zIndex: '2147483647',
    pointerEvents: 'none',
    opacity: 0,
    transition: 'opacity 0.2s ease-in-out',
    border: '1px solid rgba(255, 255, 255, 0.2)',
  });
  hint.textContent = '↑ Press Up Arrow to select parent element';
  document.body.appendChild(hint);
  return hint;
}

function copyLayoutOverlay() {
  const existing = document.getElementById('builder-copy-layout-overlay');
  if (existing) {
    return existing;
  }
  const overlay = document.createElement('div');
  overlay.id = 'builder-copy-layout-overlay';
  Object.assign(overlay.style, {
    position: 'fixed',
    top: '0',
    zIndex: '2147483647',
    left: '0',
    width: '100%',
    height: '100%',
    boxShadow: `inset 0 0 0 4px ${primaryColor}, 0 0 20px rgba(255, 255, 255, 0.2)`,
    pointerEvents: 'none',
    opacity: 0,
  });
  document.body.appendChild(overlay);

  // Show the keyboard hint when overlay is created
  const hint = getKeyboardHintBox();
  hint.style.opacity = '1';

  return overlay;
}

let hoveringElement: HTMLElement | null = null;

// On keydown of arrow up key, set the parent element to the hovering element
// and update the copy layout overlay to the parent element
document.addEventListener('keydown', e => {
  if (!isCopyLayout) {
    return;
  }
  if (e.key === 'ArrowUp') {
    e.preventDefault();
    const currentHoveringElement = hoveringElement;
    hoveringElement = hoveringElement?.parentElement;
    // Make sure that the new hovering element is not the same exact size/position as the current,
    // otherwise we'll just keep going up the DOM tree until we find a different element
    while (
      hoveringElement &&
      hoveringElement.getBoundingClientRect().top ===
        currentHoveringElement?.getBoundingClientRect().top &&
      hoveringElement.getBoundingClientRect().left ===
        currentHoveringElement?.getBoundingClientRect().left &&
      hoveringElement.getBoundingClientRect().width ===
        currentHoveringElement?.getBoundingClientRect().width &&
      hoveringElement.getBoundingClientRect().height ===
        currentHoveringElement?.getBoundingClientRect().height
    ) {
      hoveringElement = hoveringElement?.parentElement;
    }
    if (!hoveringElement || !document.documentElement.contains(hoveringElement)) {
      hoveringElement = currentHoveringElement;
      return;
    }
    updateCopyLayoutOverlay();
  }
  if (e.key === 'ArrowDown') {
    e.preventDefault();
    // set the hovering element to be one element down closer to the element
    // the mouse is directly over
    const mouseOverElement = document.elementFromPoint(clientX, clientY);
    if (mouseOverElement) {
      hoveringElement = mouseOverElement as HTMLElement;
      updateCopyLayoutOverlay();
    }
  }
});

function updateCopyLayoutOverlay() {
  if (!hoveringElement) {
    return;
  }
  const rect = hoveringElement.getBoundingClientRect();
  Object.assign(copyLayoutOverlay().style, {
    top: `${rect.top}px`,
    left: `${rect.left}px`,
    width: `${rect.width}px`,
    height: `${rect.height}px`,
    opacity: 1,
  });
}

document.addEventListener('scroll', e => {
  if (!isCopyLayout || !hoveringElement) {
    return;
  }
  updateCopyLayoutOverlay();
});

let clientX, clientY;

document.addEventListener('mousemove', e => {
  clientX = e.clientX;
  clientY = e.clientY;
  if (!isCopyLayout) {
    return;
  }
  Object.assign(getCursorMessage().style, {
    opacity: 1,
    top: `${e.clientY + 5}px`,
    left: `${e.clientX + 10}px`,
  });
});

function getCursorMessage() {
  const existing = document.getElementById('builder-cursor-message');
  if (existing) {
    return existing;
  }
  const message = document.createElement('div');
  message.id = 'builder-cursor-message';
  document.body.appendChild(message);
  Object.assign(message.style, {
    position: 'fixed',
    zIndex: '2147483647',
    pointerEvents: 'none',
    opacity: 0,
    padding: '3px 10px',
    backgroundColor: primaryColor,
    color: 'white',
    fontWeight: 500,
    fontSize: '14px',
    fontFamily: 'Inter, Helvetica, sans-serif',
    borderRadius: '100px',
  });
  message.textContent = 'Copy Layout';
  return message;
}

document.addEventListener('mouseover', e => {
  if (!isCopyLayout) {
    return;
  }
  const target = e.target as HTMLElement;
  hoveringElement = target;
  updateCopyLayoutOverlay();
});

function sendMessagePromise(message: any): Promise<any> {
  return new Promise(resolve => {
    chrome.runtime.sendMessage(message, response => {
      resolve(response);
    });
  });
}

chrome.runtime.onMessage.addListener(async message => {
  if (message.type === 'copyLayout') {
    console.log('Starting copy layout');
    isCopyLayout = true;
    document.addEventListener(
      'click',
      async e => {
        if (!isCopyLayout) {
          return;
        }
        let subject = hoveringElement || (e.target as HTMLElement);
        const subjectRect = subject.getBoundingClientRect();
        // loop up the parents and grab the highest parent that has the same dimensions
        while (subject.parentElement) {
          const parentRect = subject.parentElement.getBoundingClientRect();
          if (parentRect.width === subjectRect.width && parentRect.height === subjectRect.height) {
            subject = subject.parentElement;
          } else {
            break;
          }
        }

        e.preventDefault();
        e.stopPropagation();
        e.stopImmediatePropagation();

        closeOut();

        const loadingIndicator = getLoadingIndicator();
        // Style the loading indicator
        Object.assign(loadingIndicator.style, {
          position: 'fixed',
          top: '0',
          left: '0',
          width: '100%',
          height: '100%',
          backgroundColor: 'rgba(0, 0, 0, 0.8)',
          backdropFilter: 'blur(10px)',
          zIndex: '2147483647',
        });
        // Add a loading message
        const loadingMessage = document.createElement('div');
        loadingMessage.id = 'builder-loading-message';
        loadingIndicator.appendChild(loadingMessage);
        // Style the loading message
        Object.assign(loadingMessage.style, {
          position: 'absolute',
          top: '50%',
          left: '50%',
          color: 'white',
          transform: 'translateX(-50%)',
          fontWeight: 'bold',
          fontSize: '24px',
        });
        loadingMessage.innerHTML = `
          <div style="text-align: center">
            <div>Copying layout...</div>
            <div style="font-size: 18px; margin-top: 18px; opacity: 0.8; font-weight: 300">Grabbing multiple screen sizes for responsive layouts</div>
          </div>
        `;

        await delay(1);

        const out = domNodeToElement(subject as HTMLElement);
        out.meta = { naturalWidth: subject.getBoundingClientRect().width };

        map = elementsById(out);
        const currentWidth = window.innerWidth;
        await sendMessagePromise({ action: 'resizeWindow', width: 800 });

        const mediumOut = domNodeToElement(subject as HTMLElement, map);
        const mediumMap = elementsById(mediumOut);
        appendDifferencesForSize('medium', mediumMap, map, {});

        await sendMessagePromise({ action: 'resizeWindow', width: 300 });

        const smallOut = domNodeToElement(subject as HTMLElement);
        const smallMap = elementsById(smallOut);
        appendDifferencesForSize('small', smallMap, map, mediumMap);

        if (!out.responsiveStyles?.large?.backgroundColor) {
          // Find the first parent with a non-transparent background color
          let current = subject.parentElement;
          while (current) {
            if (getComputedStyle(current).backgroundColor !== 'rgba(0, 0, 0, 0)') {
              if (!out.responsiveStyles.large) {
                out.responsiveStyles.large = {};
              }
              out.responsiveStyles.large.backgroundColor =
                getComputedStyle(current).backgroundColor;
              break;
            }
            current = current.parentElement;
          }
        }
        delete out.responsiveStyles!.large!.marginTop;
        delete out.responsiveStyles!.large!.marginLeft;
        delete out.responsiveStyles!.large!.marginBottom;
        delete out.responsiveStyles!.large!.marginRight;
        if (out.responsiveStyles?.medium) {
          delete out.responsiveStyles!.medium!.marginTop;
          delete out.responsiveStyles!.medium!.marginLeft;
          delete out.responsiveStyles!.medium!.marginBottom;
          delete out.responsiveStyles!.medium!.marginRight;
        }
        if (out.responsiveStyles?.small) {
          delete out.responsiveStyles!.small!.marginTop;
          delete out.responsiveStyles!.small!.marginLeft;
          delete out.responsiveStyles!.small!.marginBottom;
          delete out.responsiveStyles!.small!.marginRight;
        }

        await sendMessagePromise({ action: 'resizeWindow', width: currentWidth });
        let didWriteToClipboard = false;
        try {
          await navigator.clipboard.writeText(JSON.stringify({ blocks: [out] }));
          didWriteToClipboard = true;
        } catch (err) {
          // Download the file as JSON
          const a = document.createElement('a');
          a.href =
            'data:text/json;charset=utf-8,' +
            encodeURIComponent(JSON.stringify({ data: { blocks: [out] } }));
          a.download = 'layout.builder.json';
          a.click();
        }

        loadingIndicator.onclick = () => loadingIndicator.remove();
        loadingMessage.innerHTML = `
          <div style="text-align:center">
            <div style="font-size:38px;margin-bottom:15px">${
              didWriteToClipboard ? 'Copied!' : 'Downloaded!'
            }</div>
            <div style="font-size:22px;margin-bottom:20px;font-weight:normal">Now ${
              didWriteToClipboard ? 'paste' : 'upload'
            } in Builder.io or Figma</div>
            <a style="font-size:16px;font-weight:bold;text-decoration:none;color:#87CEEB;border:2px solid #87CEEB;padding:8px 16px;border-radius:4px;display:inline-block" 
               href="https://www.builder.io/c/docs/chrome-extension" 
               target="_blank">Learn more</a>
          </div>`;

        await delay(15000);
        loadingIndicator.remove();
      },
      { once: true }
    );
  }
});

const plainTags = false;

// TODO: go back to default style checking...
const longHandAllowList = new Set([
  'paddingRight',
  'paddingBottom',
  'paddingLeft',
  'paddingTop',
  'backgroundColor',
  'backgroundImage',
  'backgroundRepeat',
  'backgroundPosition',
  'marginRight',
  'marginBottom',
  'marginLeft',
  'marginTop',
  'borderColor',
  'borderStyle',
  'borderSize',
  'flexDirection',
  'flexBasis',
  'flexGrow',
  'flexShrink',
  'flexWrap',
  'fontStyle',
  'fontFamily',
  'fontWeight',
  'fontSize',
  'transform',
  'mixBlendMode',
]);

const isNumberString = (str: string) => String(parseFloat(str)) === str;

if (!(window as any).builderIdMap) {
  (window as any).builderIdMap = new WeakMap<Element, string>();
}

const shorthandCssAllowlist = ['background', 'padding', 'margin', 'border', 'flex', 'font'];

function generateId() {
  // TODO: use UUID?
  return 'builder-' + (Date.now().toString(36) + Math.random().toString(36)).replace('.', '');
}

interface StringMap {
  [key: string]: string | undefined | null;
}

const camelCaseToKebabCase = (str?: string) =>
  str ? str.replace(/([A-Z])/g, g => `-${g[0].toLowerCase()}`) : '';

function mapToCss(map: StringMap, spaces = 2) {
  return Object.keys(map).reduce((memo, key) => {
    const value = map[key];
    return memo + `\n${' '.repeat(spaces)}${camelCaseToKebabCase(key)}: ${value};`;
  }, '');
}

const includeProperty = (property: string, value: string) => {
  if (
    property.toLowerCase().includes('inline') ||
    property.toLowerCase().includes('block') ||
    property.toLowerCase().includes('list') ||
    property.toLowerCase().includes('start') ||
    property.toLowerCase().includes('end') ||
    property.toLowerCase().includes('column') ||
    property.toLowerCase().includes('place')
  ) {
    return false;
  }

  const noInclude0 = new Set(['border', 'outline']);
  if (noInclude0.has(property)) {
    return !value.match(/(^|\s)0px/);
  }

  const noNone = new Set(['outline', 'textDecoration', 'textEmphasis', 'borderStyle']);
  if (noNone.has(property)) {
    return !value.includes('none');
  }

  return !removeProperties.has(property);
};

const removeProperties = new Set([
  // Shorthands
  'transition',
  'overflow',
  'unicodeBidi',
  'inset',
  'borderBottomColor',
  'borderRightColor',
  'borderLeftColor',
  'borderTopColor',
  'caretColor',
  'textWrapMode',
  'grid',
]);

// TODO: remove inherited (for sketch too)
function getAppliedComputedStyles(element: Element, id: string): { [key: string]: string } {
  if (!(element instanceof HTMLElement || element instanceof SVGElement)) {
    return {};
  }
  const defaultStyles = getDefaultStyles();

  const css: { [key: string]: string } = {};
  const currentComputed = { ...getComputedStyle(element) };
  const currentDisplay = element.style.display;
  element.style.setProperty('display', 'none', 'important');
  const computedStyle_ = getComputedStyle(element);
  const computedStyle = { ...computedStyle_ };
  computedStyle.display = currentComputed.display;
  element.style.display = currentDisplay;

  if (currentComputed.mixBlendMode !== 'normal') {
    css.mixBlendMode = currentComputed.mixBlendMode;
  }

  // For each computed style, if it's not the default, add it to the css object
  for (const property in computedStyle) {
    if (computedStyle.hasOwnProperty(property) && !isNumberString(property)) {
      const value = computedStyle[property];

      if (value !== defaultStyles[property] && includeProperty(property, value)) {
        css[property] = value;
      }
    }
  }

  if (computedStyle_.transform && computedStyle_.transform !== 'none') {
    css.transform = computedStyle_.transform;
  }

  if (currentComputed.height === 'auto') {
    css.height = 'auto';
  }
  if (currentComputed.width === 'auto') {
    css.width = 'auto';
  }

  for (const shorthandProperty of shorthandCssAllowlist /*shorthandCssProperties */) {
    for (const fullProperty in computedStyle) {
      if (!computedStyle.hasOwnProperty(fullProperty) || isNumberString(fullProperty)) {
        continue;
      }
      if (fullProperty !== shorthandProperty && fullProperty.startsWith(shorthandProperty)) {
        const value = computedStyle[fullProperty];
        if (
          value &&
          longHandAllowList.has(fullProperty) /* value !== defaultStyles[fullProperty] */
        ) {
          // TODO: test that this is not the default...........
          css[fullProperty] = value;
        }
      }
      delete css[shorthandProperty];
    }
  }

  const existing = map && map[id];
  const existingStyles = {
    ...(existing && existing.responsiveStyles && existing.responsiveStyles.large),
    ...(existing && existing.responsiveStyles && existing.responsiveStyles.medium),
  } as { [key: string]: string };

  for (const property in css) {
    if (typeof property !== 'string') {
      continue;
    }
    if (property === 'display' || isNumberString(property)) {
      // Always keep this
      continue;
    }
    const value = css[property];
    const propertyInExisting = Boolean(existingStyles[property]);

    if (!value) {
      delete css[property];
    }

    if (property.startsWith('border') && value.startsWith('0px')) {
      delete css[property];
    }

    if (value === (defaultStyles as any)[property] && !propertyInExisting) {
      delete css[property];
    }

    if (
      (value === 'initial' || value === 'inherit') /* maybe: || value === '0px' */ &&
      !propertyInExisting
    ) {
      delete css[property];
    }

    // Browser prefixes
    if (property.match(/^(webkit|ms|o|moz)[A-Z]/)) {
      delete css[property];
    }

    if (value) {
      // Has a CSS variable
      if (value.includes('--')) {
        css[property] = (computedStyle as any)[property];
      }
    }
  }

  for (const property in existingStyles) {
    if (existingStyles.hasOwnProperty(property) && !isNumberString(property)) {
      if (
        !css[property] &&
        (computedStyle as any)[property] !== (existingStyles as any)[property]
      ) {
        // TODO: display: none style...
        css[property] = (computedStyle as any)[property];
      }
    }
  }

  // Convert relative to absolute
  for (const property of ['background', 'backgroundImage']) {
    const value = css[property];
    if (typeof value === 'string') {
      const urlRe = /url\s*\(\s*(['"]?)([^'"\)]+?)(['"]?)\s*\)/i;
      css[property] = value.replace(urlRe, (_everything, openQuote, src, closeQuote) => {
        const absolute = new URL(src, location.href).href;
        return `url(${openQuote}${absolute}${closeQuote})`;
      });
    }
  }

  return css;
}

function getDefaultStyles() {
  let defaultStyleDiv = document.getElementById('default-style-div');

  if (!defaultStyleDiv) {
    defaultStyleDiv = document.createElement('div');
    document.body.appendChild(defaultStyleDiv);
    defaultStyleDiv.id = 'default-style-div';
  }
  defaultStyleDiv.style.display = 'none';
  const defaultStyles = getComputedStyle(defaultStyleDiv);
  return {
    ...defaultStyles,
    display: 'block',
  };
}

function domNodeToElement(element: HTMLElement | Node, includeChildren = true): BuilderElement {
  if (element instanceof Node && element.nodeType === Node.TEXT_NODE) {
    const content = element.textContent || '';
    return {
      '@type': '@builder.io/sdk:Element',
      tagName: 'span',
      component: {
        name: 'Text',
        options: {
          text: content,
        },
      },
    };
  }

  if (!(element instanceof HTMLElement || element instanceof SVGElement)) {
    // TODO: delete this and return null instead
    return {
      '@type': '@builder.io/sdk:Element',
      tagName: 'span',
      responsiveStyles: {
        large: {
          display: 'inline',
        },
      },
    };
  }

  if (
    element.id === 'builder-copy-layout-overlay' ||
    element.id === 'builder-keyboard-hint' ||
    element.id === 'builder-loading-indicator' ||
    element.id === 'builder-cursor-message'
  ) {
    return null;
  }

  let useChildren: null | BuilderElement[] = null;

  const id = builderIdMap.get(element) || generateId();
  builderIdMap.set(element, id);
  const appliedStyles = getAppliedComputedStyles(element, id);
  const meta: any = {};

  const properties: { [key: string]: string } = {};

  for (let i = 0; i < element.attributes.length; i++) {
    const attr = element.attributes[i];
    if (
      !['builder-id', 'id', 'class', 'style'].includes(attr.name) &&
      attr.name.match(/^[a-z0-9-_]+$/i) &&
      !attr.name.startsWith('data-')
    ) {
      properties[attr.name] = attr.value;
    }
  }

  let component: any = undefined;
  let tagName = element.tagName.toLowerCase();
  // TODO: text.....
  if (tagName === 'body') {
    tagName = 'div';
  }

  if (tagName === 'iframe') {
    tagName = 'span';
  }
  if (tagName === 'input') {
    properties.type = (element.getAttribute('type') || undefined) as string;
  }

  if (tagName === 'svg') {
    tagName = 'div';
    let htmlWidth = element.getAttribute('width');
    let htmlHeight = element.getAttribute('height');
    if (htmlWidth) {
      if (!(htmlWidth.endsWith('px') || htmlWidth.endsWith('%'))) {
        htmlWidth = htmlWidth + 'px';
      }
      appliedStyles.width = htmlWidth;
    }
    if (htmlHeight) {
      if (!(htmlHeight.endsWith('px') || htmlHeight.endsWith('%'))) {
        htmlHeight = htmlHeight + 'px';
      }
      appliedStyles.height = htmlHeight;
    }

    // TODO: maybe clone and get all this and then get outerHTML
    // so not to cause reflows bc
    const cloned = element.cloneNode(true) as SVGElement;
    cloned.setAttribute('style', mapToCss(appliedStyles));
    for (const el of Array.from(cloned.querySelectorAll('*'))) {
      if (el instanceof SVGElement) {
        // TODO: apply all computed styles from css to inline styles.......
        const fill = getComputedStyle(el).fill;
        if (fill !== 'rgb(0, 0, 0)') {
          el.style.fill = fill;
        }
        const display = getComputedStyle(el).display;
        if (display !== 'inline') {
          el.style.display = display;
        }
      }
    }

    component = {
      name: 'Custom Code',
      options: {
        code: cloned.outerHTML,
      },
    };
  }

  function chooseBestSrc(element: HTMLPictureElement) {
    let currentSrc = element.querySelector('img')?.currentSrc;
    if (currentSrc) {
      return currentSrc;
    }

    // TODO: choose the source with largest min-width, and the srcset
    // with largest w
    const sources = element.querySelectorAll('source');
    const bestSource = Array.from(sources).find(tag => {
      const mql = window.matchMedia(tag.media);
      return mql.matches;
    });
    const src = (bestSource || sources[0])?.srcset.split(/\s/)[0];
    if (src) {
      return src;
    }

    const img = element.querySelector('img');
    if (img && img.src && !img.src.startsWith('data:')) {
      return img.src;
    }
    return '';
  }
  if (tagName === 'picture') {
    const img = element.querySelector('img');
    if (img) {
      useChildren = [domNodeToElement(img)].filter(el => el !== null);
    }

    if (useChildren?.[0]) {
      const source = chooseBestSrc(element as HTMLPictureElement);
      if (source && useChildren[0].component) {
        useChildren[0].component.options.image = new URL(source, location.href).href;
      }
    }
    tagName = 'span';
  }

  if (tagName === 'img') {
    if (plainTags) {
      // Do nothing
    } else {
      const src = (
        (element as HTMLImageElement).currentSrc ||
        (element as HTMLImageElement).src ||
        properties.src ||
        ((element as HTMLImageElement).srcset && (element as HTMLImageElement).srcset.split(' ')[0])
      )?.replace(/,$/, '');

      const absolute = new URL(src, location.href).href;
      meta.originalAspectRatio =
        (element as HTMLImageElement).height / (element as HTMLImageElement).width;
      component = {
        name: 'Raw:Img',
        options: {
          image: absolute,
          // TODO: add defaults here
        },
      };
      delete properties.src;
      delete properties.srcset;
      delete properties.sizes;
      tagName = '';
    }
  }

  const childNodesWithoutComments = Array.from(element.childNodes).filter(
    item => item.nodeType !== Node.COMMENT_NODE
  );

  // TODO: other tags may need this too
  if (tagName === 'button') {
    if (!appliedStyles.backgroundColor) {
      appliedStyles.backgroundColor = 'rgba(0, 0, 0, 0)';
    }
    if (!appliedStyles.borderColor) {
      appliedStyles.borderColor = 'rgba(0, 0, 0, 0)';
    }
  }

  const children = Array.from(element.childNodes)
    .slice()
    .filter(el => [Node.TEXT_NODE, Node.ELEMENT_NODE].includes(el.nodeType as any))
    .filter(node => {
      if (node.nodeType === Node.TEXT_NODE) {
        const textContent = node.textContent;
        if (!textContent.trim()) {
          return false;
        }
      }
      return true;
    });

  const isLink = element.tagName === 'A';
  if (isLink) {
    delete properties.href;
  }
  if (element.tagName === 'IMG') {
    delete properties.src;
    delete properties.srcset;
    delete properties.srcSet;
    delete properties.sizes;
  }

  return {
    '@type': '@builder.io/sdk:Element',
    id: id!,
    tagName,
    component,
    meta: Object.keys(meta).length ? meta : undefined,

    ...(component?.name === 'Custom Code' && {
      layerName: 'SVG',
    }),
    responsiveStyles: {
      large: {
        ...(tagName === 'body' && { overflow: 'auto' }),
        ...appliedStyles,
      },
    },
    properties: properties,
    children: useChildren
      ? useChildren
      : includeChildren
      ? Array.from(children)
          .filter(el =>
            el instanceof HTMLElement
              ? ![
                  'script',
                  'style',
                  'noscript',
                  'link',
                  'meta',
                  'area',
                  'base',
                  'col',
                  'embed',
                  'link',
                  'meta',
                  'param',
                  'source',
                  'track',
                  'wbr',
                ].includes(el.tagName.toLowerCase())
              : el instanceof SVGElement
              ? el.tagName.toLowerCase() === 'svg'
              : true
          )
          .filter(el =>
            el instanceof HTMLElement || el instanceof SVGElement
              ? !el.classList.contains('default-style-div')
              : true
          )
          .map(el => domNodeToElement(el))
          .filter(el => el !== null)
      : [],

    ...({
      // TODO: element model should get/set this from href and apply/remove h tag on doing that
      linkUrl: isLink ? (element as HTMLAnchorElement).href : undefined,
    } as any),
  };
}

function closeOut() {
  document.getElementById('builder-loading-indicator')?.remove();
  document.getElementById('builder-keyboard-hint')?.remove();
  if (!isCopyLayout) {
    return;
  }
  isCopyLayout = false;

  Object.assign(copyLayoutOverlay().style, {
    opacity: 0,
  });
  Object.assign(getCursorMessage().style, {
    opacity: 0,
  });
}

// on escape key, escape from overlays
document.addEventListener('keydown', e => {
  if (e.key === 'Escape') {
    closeOut();
  }
});

function getLoadingIndicator() {
  const current = document.getElementById('builder-loading-indicator');
  if (current) {
    return current;
  }
  const loadingIndicator = document.createElement('div');
  loadingIndicator.id = 'builder-loading-indicator';
  document.body.appendChild(loadingIndicator);
  return loadingIndicator;
}

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
  if (message.type === 'outlineElements') {
    const { shouldOutline, builderOutlineStylesClass, builderEditClassName } = message.payload;

    if (shouldOutline) {
      const contentRootAttrName = 'builder-content-id';
      const contentIdSelector = '[' + contentRootAttrName + ']';
      const builderOutlineStyleTag = document.createElement('style');
      builderOutlineStyleTag.type = 'text/css';
      builderOutlineStyleTag.innerHTML = `
        ${contentIdSelector}:not([builder-content-id=""]) { border: 4px solid #1A73E8 !important } 
        ${contentIdSelector}:hover > .${builderEditClassName} { display: inline-block !important }
        .builder-inline-symbol > * > ${contentIdSelector} { border: none !important }
      `;
      builderOutlineStyleTag.classList.add(builderOutlineStylesClass);
      document.head.appendChild(builderOutlineStyleTag);

      const builderComponents = Array.from(document.querySelectorAll(contentIdSelector));
      builderComponents.forEach((el: HTMLElement) => {
        if (!el.style.position || el.style.position === 'static') {
          el.style.position = 'relative';
        }

        let extraTopPadding = 0;
        const nestedComponents = Array.from(el.querySelectorAll(contentIdSelector));
        const parentDimensions = el.getBoundingClientRect();

        if (nestedComponents.length) {
          nestedComponents.forEach(nestedComponent => {
            const nestedDimensions = nestedComponent.getBoundingClientRect();
            const yRelativeToParent = nestedDimensions.top - parentDimensions.top;
            const xRelativeToParent = nestedDimensions.left - parentDimensions.left;

            if (yRelativeToParent < 100 && xRelativeToParent < 100) {
              if (extraTopPadding < nestedDimensions.height) {
                extraTopPadding = nestedDimensions.height;
              }
            }
          });
        }

        let baseTopPadding = 0;
        if (parentDimensions.height > 50) {
          baseTopPadding = 10;
        }

        const contentIdAttribute =
          el.children[0]?.attributes['data-builder-content-id'] ||
          el.attributes['builder-content-id'];
        const contentId = contentIdAttribute && contentIdAttribute.value;
        const isInlineSymbol =
          el.parentElement?.parentElement?.classList.contains('builder-inline-symbol');
        const isSFCC =
          Array.from(document.querySelectorAll('section[data-source="builder-sfcc-sync"]')).length >
          0;

        if (contentId && !isInlineSymbol) {
          const editButton = document.createElement('a');
          editButton.innerText = 'EDIT';
          const params = isSFCC
            ? '?overridePreviewUrl=' + encodeURIComponent(window.location.href)
            : '';
          editButton.href = 'https://builder.io/content/' + contentId + params;
          editButton.target = '_blank';
          editButton.classList.add(builderEditClassName);
          Object.assign(editButton.style, {
            display: 'none',
            'background-color': '#1A73E8',
            'z-index': 100,
            cursor: 'pointer',
            'box-shadow':
              'rgba(0, 0, 0, 0.2) 0px 1px 3px 0px, rgba(0, 0, 0, 0.14) 0px 1px 1px 0px, rgba(0, 0, 0, 0.12) 0px 2px 1px -1px',
            position: 'absolute',
            top: extraTopPadding + baseTopPadding + 'px',
            left: '10px',
            padding: '10px 20px',
            'font-size': '16px',
            color: 'white',
            'max-width': '100px',
            'border-radius': '4px',
            'text-align': 'center',
          });

          el.appendChild(editButton);
        }
      });
    } else {
      const allStyleTags = Array.from(document.getElementsByClassName(builderOutlineStylesClass));
      allStyleTags.forEach(el => el.remove());
      const allEditButtons = Array.from(document.getElementsByClassName(builderEditClassName));
      allEditButtons.forEach(el => el.remove());
    }
  }
  // ... existing message handlers
});
